import React from "react"
import Contatos from "./Contatos"
import Educacao from "./Educacao"

export default class Sidebar extends React.Component {
  render() {
    return (
      <>
        <aside>
          <div className="sidebar">
            <h3>Contatos</h3>
            {this.props.contatosmap.map((item) => (
              <Contatos conteudo={item} key={item.id} />
            ))}
          </div>

          <div className="sidebar">
            <h3>Educação</h3>
            {this.props.educacaomap.map((item) => (
              <Educacao conteudo={item} key={item.id} />
            ))}
          </div>
        </aside>
      </>
    )
  }
}
